<template>
  <v-container
    class="text-center"
    fluid
    style="top: 0; margin: 3rem 0; color: #eef0f2;"
  >
    <v-row style="margin: 3rem;">
      <v-select
        :menu-props="{ bottom: true, offsetY: true }"
        color="#e67e22"
        rounded
        dark
        solo
        label="All Commands"
        :items="categories"
      ></v-select>
      <v-btn style="margin-left: 1rem;" color="#e67e22">
        Filter
      </v-btn>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Commands",
  data: () => ({
    categories: ["Info", "Mod"],
  }),
  mounted() {
    const links = Array.from(document.querySelectorAll(".link"));
    const active = links.find(
      (x) => x.innerText === this.$route.name.toUpperCase()
    );
    if (active) active.classList.toggle("active");
  },
};
</script>
